@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');
@import 'Variables';
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  color: rgb(97, 218, 251);
}
body{
  background-color: #282c34;
}
.App {
  text-align: center;
}
.Links{
  margin: 0 auto;
  max-width: 1000px;
  
  display: flex;
  flex-direction: column;
}
.Link{
  width: 100%;
  display: grid;
  /* grid-template-columns: 100px 1fr 200px; */
  grid-template-columns: 1fr 8fr 4fr;
  border-bottom: 1px solid rgb(97, 218, 251);
  word-break: break-word;
  padding: 5px 0;
}
.Link:last-child{
  border-bottom: none;
}
.Link button{
  width: 100%;
  color: black;
  padding: 20px 20px;
  /* height: 40px; */
  /* h100% */
  cursor: pointer;
}
.Link a{
  color: black;
  text-decoration: none;
  cursor:auto;
}
.Linkimg{
  width: 100%;
  min-width: 30px;
  max-width: 50px;
  cursor: pointer;
}
.Linkimg2{
  min-width: 50px;
  /* max-width: 60px; */
  width: 100%;
  cursor: pointer;
}
.Link img:hover{
  /* opacity: 0.9; */
}
.Link h2{
  font-size: 2.7vw;
  /* font-size: 18px; */
  font-weight: normal;
  font-weight: 700;
}
.Link pre{
  font-size: 14px;
  text-align: left;
  padding: 10px 0;
}
.stack{
  padding: 10px;
  // border-bottom:1px solid $baza__color;
  // width: 85%;
  & ul li{
    color: $nice2__color;
  }
  & p{
    // color: $nice__color;
    text-align: left;
    border-bottom: 1px solid $nice2__color;
    width: fit-content;
    padding-bottom: 4px;
  }
}
.tips{
  padding: 10px;
  & ul li{
    color: $nice2__color;
  }
  & p{
    // color: $nice2__color;
    text-align: left;
    border-bottom: 1px solid $nice2__color;
    width: fit-content;
    padding-bottom: 4px;
  }
}
ul{
  font-size: 14px;
  text-align: left;
  padding: 10px;
}
li{
  text-align: left;
  font-size: 2vw;
}
@media (min-width:600px){
  li{
    font-size: 14px;
  }
  .Link h2{
    font-size: 18px;
  }
}


.myModal {
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.637);
  margin: 0 auto;
}
.myModalContent {
  /* width: 100vw; */
  padding: 15px;
  background: rgba(78, 92, 119, 0.907);
  border-radius: 5px;
}
.myModalContent img{
  width: 100%;
  height: 100%;
}
.myModal.active {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}